<template>
  <v-card flat tile width="100%" class="white--text text-center mb-3">
    <v-img
      class="white--text align-end"
      height="300px"
      :src="mainImage"
      v-if="mainImage"
    >
      <v-card-text class="text-center">
        <div class="display-1 white--text text-center pa-3">{{ title }}</div>
        <div class="d-flex flex-nowrap text-center justify-center" v-if="tabs">
          <div v-for="(tab, index) in tabs" :key="index">
            <v-btn
              class="white--text mr-2"
              :class="{ success: tab.active }"
              outlined
              @click="changeTab(tab.id)"
            >
              <v-icon>{{ tab.tabIcon }}</v-icon>
              {{ tab.title }}
            </v-btn>
          </div>
        </div>
      </v-card-text>
    </v-img>
  </v-card>
</template>

<script>
export default {
  props: {
    title: String,
    mainImage: String,
    tabIcon: String,
    tabs: Array
  },
  methods: {
    changeTab(tabId) {
      this.$emit("changeTab", tabId);
    }
  }
};
</script>



