      menus: [
        {
          title: "사업안내",
          icon: "mdi-information",
          model: false,
          submenus: [
            {
              title: "사업개요",
              path: "/overview",
              icon: "mdi-information"
            },
            {
              title: "모집공고",
              path: "/gonggo",
              icon: "mdi-file-cloud-outline"
            }
          ]
        },
        {
          title: "입지안내",
          icon: "mdi-map-marker",
          model: false,
          submenus: [
            {
              title: "입지환경",
              path: "/environment",
              icon: "mdi-map-marker"
            },
            {
              title: "프리미엄",
              path: "/premium",
              icon: "mdi-google-circles-extended"
            }
          ]
        },
        {
          title: "단지안내",
