          title: "세대안내",
          icon: "mdi-file-chart",
          model: false,
          submenus: [
            {
              title: "유니트",
              path: "/unit",
              icon: "mdi-consolidate"
            },
            {
              title: "인테리어",
              path: "/interior",
              icon: "mdi-cube-send"
            },
            {
              title: "VR파노라마",
              path: "/vr",
              icon: "mdi-virtual-reality"
            }
          ]
        },
        {
          title: "고객센터",
          icon: "mdi-information",
          model: false,
          submenus: [
            {
              title: "관심고객등록",
              path: "/interest",
              icon: "mdi-account-plus-outline"
            },
            {
              title: "홍보관안내",
              path: "/information",
              icon: "mdi-floor-plan"
            }
          ]
        }
      ]
    };
  },
  watch: {
    group() {
      this.drawer = false;
    }
  },
  components: {
    BaseHeader,
    BaseFooter,
    NavDrawer,
    AppBar
  }
};
</script>



